import defaultValue from '../core/defaultValue';
import PostProcessing from './PostProcessing';
import shader from '../shader/postProcessStage/snow'

class SnowEffect extends PostProcessing {
  /**
   * 
   * @extends PostProcessing
   * @param {object} options 
   * @param {number} [options.thickness=0.8] 0~1, 01
   * @param {number} [options.density=10.0] 
   * @param {number} [options.speed=350.0] 
   *
   * @see {@link https://www.giserdqy.com/gis/opengis/3d/cesium/7311/|Cesium}
   *
   */
  constructor(options) {
    super(options);
    this._fragmentShader = defaultValue(this._options.fragmentShader, shader);
    this._thickness = defaultValue(this._options.thickness, 0.5);
    this._density = defaultValue(this._options.density, 10.0);
    this._speed = defaultValue(this._options.speed, 350);
    const uniforms = {
      alpha: () => {
        return this.thickness
      },
      snowDensity: () => {
        return this.density
      },
      speed: () => {
        return this.speed
      }
    }
    this._uniforms = defaultValue(this._options.uniforms, uniforms);

  }
  /**
   * 0~1
   * @type {number} 
   */
  get thickness() {
    return Cesium.Math.clamp(this._thickness, 0, 1);
  }
  set thickness(val) {
    this._thickness = val;
  }
  /**
   * 2~20
   * @type {number} 
   */
  get density() {
    return Cesium.Math.clamp(this._density, 0, 20);
  }
  set density(val) {
    this._density = val;
  }
  /**
   * 
   * @type {number} 
   */
  get speed() {
    const speed = this._speed;
    return 10.0 + Cesium.Math.clamp((390 - speed), 0, 390);
  }
  set speed(val) {
    this._speed = val;
  }
  /**
   * 
   * @override
   * @return {Cesium.PostProcessStage} 
   */
  createPostStage() {
    return new Cesium.PostProcessStage({
      fragmentShader: this._fragmentShader,
      uniforms: this._uniforms
    })
  }
}
export default SnowEffect;
