import defaultValue from '../core/defaultValue';
import PointBaseGraphic from './PointBaseGraphic';
import Url from '../core/Url';
import glsl from '../shader/circleScan'
const {
    MaterialAppearance,
    Material
} = Cesium;
class CircleScanGraphic extends PointBaseGraphic {
    /**
     * 
     * @extends PointBaseGraphic
     * @param {CircleScanGraphic.ConstructorOptions} options 
     * @example
     * graphic = new CesiumPro.CircleScanGraphic({
     *   position: new CesiumPro.LonLat(107.766, 34.548),
     *   radius: 1000,
     *   color: '#FF0000',
     *   speed: 5000
     * });
     * graphic.addTo(viewer);
     * // or
     * viewer.graphicGraphic.add(graphic);
     * @demo {@link examples/apps/index.html#/5.3.1-circlescan|}
     */
    constructor(options) {
        super(options);
        this._speed = defaultValue(options.speed, 10);
        this._time = 0;
        this.createGraphic()
    }
    /**
     * 
     * @type {Number}
     * @default 10
     */
    get speed() {
        return this._speed;
    }
    set speed(val) {
        if (this._speed === val) {
            return;
        }
        if (val <= 0) {
            this._speed = 0;
        }
        this.definedChanged.raise('speed', val, this._speed);
        this._speed = val;
    }
    createAppearance() {
        if (this._appearance) {
            return this._appearance;
        }
        this._appearance = new MaterialAppearance({
            material: new Material({
                translucent: true,
                fabric: {
                    uniforms: {
                        image: Url.buildModuleUrl("./assets/images/radarScan.png"),
                        stRotation: Cesium.Matrix2.IDENTITY,
                        color: this._color,
                        time: 0
                    },
                    source: glsl
                },
            })
        })
        return this._appearance;
    }
    update(time) {
        if (!this.material) {
            return;
        }
        if (this.speed === 0) {
            this.material.uniforms.time = 0;
            return;
        }
        const t = Cesium.JulianDate.toDate(time).getTime() * this.speed;
        const normalizeAngle = t % (360);
        const rotation = Cesium.Matrix2.fromRotation(Cesium.Math.toRadians(-normalizeAngle));
        this.material.uniforms.stRotation = {
            x: rotation[0],
            y: rotation[1],
            z: rotation[2],
            w: rotation[3]
        };
        this.material.uniforms.time = t - parseInt(t);
    }
}
/**
 * @typedef {Object} CircleScanGraphic.ConstructorOptions
 *
 * 
 *
 * @property {LonLat} position 
 * @property {Number} radius 
 * @property {Cesium.Color | String} [color] 
 * @property {Boolean} [clampToGround = false] 
 * @property {Number} [height = 0]  
 * @property {Number} [extrudedHeight = 0]  
 * @property {Number} [stRotation = 0] 
 * @property {Object} [property] 
 * @property {Number} [speed = 10] 
 */
export default CircleScanGraphic;