import spreadMaterialShaderSource from '../shader/dynamicSpreadMaterial.js';
import defaultValue from '../core/defaultValue'
import Event from '../core/Event';
const {
  Material,
  Color,
} = Cesium;

Material.DynamicSpreadType = 'DynamicSpread'
Material._materialCache.addMaterial(Material.DynamicSpreadType, {
  fabric: {
    type: Material.DynamicSpreadType,
    uniforms: {
      color: new Color(1.0, 0.0, 0.0),
      time: 0,
      gradient: 0.0
    },
    source: spreadMaterialShaderSource
  }
})

class DynamicSpreadMaterialProperty {
  /**
   * 
   * @param {Object} [options={}] 
   * @param {Number} [options.duration=1000] 
   * @param {Color} [options.color=Cesium.Color.RED] 
   * @param {Number} [options.gradient=4.0] 00
   */
  constructor(options = {}) {
    this._color = defaultValue(options.color, Cesium.Color.RED);
    this._time = 0;
    this._duration = defaultValue(options.duration, 1000);
    this._gradient = defaultValue(options.gradient, 4.0)
    this._definitionChanged = new Event();
  }

  /**
   * 
   * @type {Cesium.Color}
   */
  get color() {
    return this._color;
  }
  get time() {
    return this._time;
  }
  /**
   * 
   * @type {Event}
   */
  get definitionChanged() {
    return this._definitionChanged
  }
  /**
   * getValue
   * @readonly
   */
  get isConstant() {
    return false;
  }
  set color(v) {
    this._color = v;
  }
  /**
   * 
   * @type {Number}
   */
  get duration() {
    return this._duration;
  }
  set duration(v) {
    this._duration = v;
  }
  /**
   * 00
   * @return {Number} 
   */
  get gradient() {
    return this._gradient;
  }
  set gradient(v) {
    this._gradient = v;
  }
  /**
   * 
   * @return {String}
   */
  getType(time) {
    return Material.DynamicSpreadType;
  }
  /**
   * 
   * @param  {JulianDate} time  
   * @param  {Object} [result] 
   * @return {Object} result,result
   */
  getValue(time, result) {
    result = defaultValue(result, {});
    result.color = this.color;
    if (this._time === undefined) {
      this._time = time.secondsOfDay;
    }
    result.time = (time.secondsOfDay - this.time) * 1000 / this.duration;
    result.gradient = this.gradient;
    return result
  }
  /**
   * 
   * @param  {DynamicSpreadMaterialProperty} other 
   * @return {Bool}   true,false
   */
  equals(other) {
    return this === other || (other instanceof DynamicSpreadMaterialProperty && this.color === other.color &&
      this.gradient === other.gradient);
  }
}

export default DynamicSpreadMaterialProperty;
