class CesiumProError extends Error {
    /**
     * CesiumPro
     * @extends Error
     * @param {String} message 
     * @example
     * function flyTo(viewer,entity){
     *    if(!viewer){
     *      throw(new CesiumPro.CesiumProError("viewer"))
     *    }
     *    viewer.flyTo(entity)
     * }
     *
     */
    constructor(message) {
        super(message);
        this.name = 'CesiumProError';
    }
}
CesiumProError.throwInstantiationError = function () {
    throw new DeveloperError(
        "This function defines an interface and should not be called directly."
    );
};
CesiumProError.throwNoInstance = function () {
    throw new CesiumProError('.')
}
export default CesiumProError;
