import defaultValue from "../core/defaultValue.js";

class XYZLayer extends Cesium.UrlTemplateImageryProvider {
    /**
     * imageX/Y/ZURLTMS,WMSWMTS
     * @extends Cesium.UrlTemplateImageryProvider
     * @param {Promise.<Object>|Object} options 
     * @param {Resource|String} options.url  url:
     * <ul>
     *     <li><code>{z}</code>: 0</li>
     *     <li><code>{x}</code>: X0</li>
     *     <li><code>{y}</code>: Y0</li>
     *     <li><code>{s}</code>: </li>
     *     <li><code>{reverseX}</code>: X0</li>
     *     <li><code>{reverseY}</code>: Y0</li>
     *     <li><code>{reverseZ}</code>: 0</li>
     *     <li><code>{westDegrees}</code>: </li>
     *     <li><code>{southDegrees}</code>: </li>
     *     <li><code>{eastDegrees}</code>: </li>
     *     <li><code>{northDegrees}</code>: </li>
     *     <li><code>{westProjected}</code>: </li>
     *     <li><code>{southProjected}</code>: </li>
     *     <li><code>{eastProjected}</code>: </li>
     *     <li><code>{northProjected}</code>: </li>
     *     <li><code>{width}</code>: </li>
     *     <li><code>{height}</code>: </li>
     * </ul>
     * @param {Resource|String} [options.pickFeaturesUrl]  URL 
     *                 {@link XYZLayer#pickFeatures} undefinedurl:
     * <ul>
     *     <li><code>{i}</code>: 0</li>
     *     <li><code>{j}</code>: 0</li>
     *     <li><code>{reverseI}</code>: 0</li>
     *     <li><code>{reverseJ}</code>: 0</li>
     *     <li><code>{longitudeDegrees}</code>: </li>
     *     <li><code>{latitudeDegrees}</code>: </li>
     *     <li><code>{longitudeProjected}</code>: </li>
     *     <li><code>{latitudeProjected}</code>: </li>
     *     <li><code>{format}</code>: , {@link Cesium.GetFeatureInfoFormat}.</li>
     * </ul>
     * @param {Object} [options.urlSchemeZeroPadding] 0urlSchemeZeroPadding : { '{x}' : '0000'}
     * x120012
     * <ul>
     *  <li> <code>{z}</code>: z</li>
     *  <li> <code>{x}</code>: x</li>
     *  <li> <code>{y}</code>: y</li>
     *  <li> <code>{reverseX}</code>: reverseX</li>
     *  <li> <code>{reverseY}</code>: reverseY</li>
     *  <li> <code>{reverseZ}</code>: reverseZ</li>
     * </ul>
     * @param {String|String[]} [options.subdomains='abc'] {s}
     * @param {Credit|String} [options.credit=''] .
     * @param {Number} [options.minimumLevel=0] 
     * @param {Number} [options.maximumLevel] 
     * @param {Rectangle} [options.rectangle=Cesium.Rectangle.MAX_VALUE] 
     * @param {TilingScheme} [options.tilingScheme=Cesium.WebMercatorTilingScheme] 
     * @param {Number} [options.tileWidth=256] 
     * @param {Number} [options.tileHeight=256] 
     * @param {Boolean} [options.hasAlphaChannel=true] alphafalsealpha
     * @param {GetFeatureInfoFormat[]} [options.getFeatureInfoFormats=XYZLayer.defaultFeatureInfoFormats]  {@link XYZLayer#pickFeatures} 
     * @param {Boolean} [options.enablePickFeatures=true] true,XYZLayer#pickFeaturespickFeaturesUrlundefined
     * @param {Object} [options.customTags] URL
     * 
     * @example
     * // Access Natural Earth II imagery, which uses a TMS tiling scheme and Geographic (EPSG:4326) project
     * const tms = new CesiumPro.XYZLayer({
     *     url : Cesium.buildModuleUrl('Assets/Textures/NaturalEarthII') + '/{z}/{x}/{reverseY}.jpg',
     *     credit : ' Analytical Graphics, Inc.',
     *     tilingScheme : new Cesium.GeographicTilingScheme(),
     *     maximumLevel : 5
     * });
     * // Access the CartoDB Positron basemap, which uses an OpenStreetMap-like tiling scheme.
     * const positron = new CesiumPro.XYZLayer({
     *     url : 'http://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}.png',
     *     credit : 'Map tiles by CartoDB, under CC BY 3.0. Data by OpenStreetMap, under ODbL.'
     * });
     * // Access a Web Map Service (WMS) server.
     * const wms = new CesiumPro.XYZLayer({
     *    url : 'https://programs.communications.gov.au/geoserver/ows?tiled=true&' +
     *          'transparent=true&format=image%2Fpng&exceptions=application%2Fvnd.ogc.se_xml&' +
     *          'styles=&service=WMS&version=1.1.1&request=GetMap&' +
     *          'layers=public%3AMyBroadband_Availability&srs=EPSG%3A3857&' +
     *          'bbox={westProjected}%2C{southProjected}%2C{eastProjected}%2C{northProjected}&' +
     *          'width=256&height=256',
     *    rectangle : Cesium.Rectangle.fromDegrees(96.799393, -43.598214999057824, 153.63925700000001, -9.2159219997013)
     * });
     * // Using custom tags in your template url.
     * const custom = new CesiumPro.XYZLayer({
     *    url : 'https://yoururl/{Time}/{z}/{y}/{x}.png',
     *    customTags : {
     *        Time: function(imageryProvider, x, y, level) {
     *            return '20171231'
     *        }
     *    }
     * });
     * // geoserver tms
     * const  geoserverTMS = new XYZLayer({
     *      url: 'http://localhost:8080/geoserver/gwc/service/tms/1.0.0/topp%3Astates@EPSG%3A4326@png/{z}/{x}/{reverseY}.png',
     *      tilingScheme: proj.get('EPSG:4326')
     *  })
     * // geoserver wmspick
     * const geoserverTMS = new XYZLayer({
     *      pickFeaturesUrl:'http://localhost:8080/geoserver/wms?service=WMS&version=1.1.1&request=GetFeatureInfo&layers=topp%3Astates&bbox={westProjected}%2C{southProjected}%2C{eastProjected}%2C{northProjected}&width={width}&height={height}&srs=EPSG%3A4326&query_layers=topp%3Astates&info_format={format}&x={i}&y={j}',
     *      url: 'http://localhost:8080/geoserver/topp/wms?service=WMS&version=1.1.1&request=GetMap&layers' +
     *          '=topp%3Astates&bbox={westDegrees}%2C{southDegrees}%2C{eastDegrees}%2C{northDegrees}&transparent=true&' +
     *           'width=768&height=330&srs=EPSG%3A4326&format=image/png',
     *      tilingScheme: proj.get('EPSG:4326'),
     *      getFeatureInfoFormats: [new Cesium.GetFeatureInfoFormat("xml", "text/xml")]
     * })
     * // geoserver WMTS
     * const layer = new XYZLayer({
     *     url: 'http://localhost:8080/geoserver/gwc/service/wmts?layer=topp%3Astates&style=&til' +
     *         'ematrixset=EPSG%3A4326&Service=WMTS&Request=GetTile&Version=1.0.0&Format=image%2Fpng&TileMatrix={TileMatrix}' +
     *         '&TileCol={tileCol}&TileRow={tileRow}',
     *     tilingScheme: proj.get('EPSG:4326'),
     *     customTags: {
     *         TileMatrix: function (imageryProvider, x, y, level) {
     *             return 'EPSG:4326:' + level
     *         },
     *         tileCol: function (imageryProvider, x, y, level) {
     *             return y
     *         },
     *         tileRow: function (imageryProvider, x, y, level) {
     *             return x
     *         }
     *     }
     * })
     */
    constructor(options) {
        if (typeof options === 'string') {
            options = {url: options}
        }
        options.getFeatureInfoFormats = defaultValue(options.getFeatureInfoFormats, XYZLayer.defaultFeatureInfoFormats);
        super(options);
    }
    /**
     * URL
     * <ul>
     *     <li><code>{z}</code>: 0</li>
     *     <li><code>{x}</code>: X0</li>
     *     <li><code>{y}</code>: Y0</li>
     *     <li><code>{s}</code>: </li>
     *     <li><code>{reverseX}</code>: X0</li>
     *     <li><code>{reverseY}</code>: Y0</li>
     *     <li><code>{reverseZ}</code>: 0</li>
     *     <li><code>{westDegrees}</code>: </li>
     *     <li><code>{southDegrees}</code>: </li>
     *     <li><code>{eastDegrees}</code>: </li>
     *     <li><code>{northDegrees}</code>: </li>
     *     <li><code>{westProjected}</code>: </li>
     *     <li><code>{southProjected}</code>: </li>
     *     <li><code>{eastProjected}</code>: </li>
     *     <li><code>{northProjected}</code>: </li>
     *     <li><code>{width}</code>: </li>
     *     <li><code>{height}</code>: </li>
     * </ul>
     * @readonly
     * @returns {String} URL
     */
    get url() {
        return super.url;
    }
    /**
     * 
     * @readonly
     * @type {Number}
     */
     get minimumLevel() {
        return super.minimumLevel;
    }
    /**
     * 
     * @type {Number}
     * @readonly
     */
    get maximumLevel() {
        return super.maximumLevel;
    }
    /**
     * 
     * @type {Cesium.Rectangle}
     * @readonly
     */
     get rectangle() {
        return super.rectangle;
    }
    /**
     * 
     * @type {Event}
     * @readonly
     */
    get errorEvent() {
        return super.errorEvent;
    }
    /**
     * 
     * @readonly
     * @type {Boolean}
     */
    get ready() {
        return super.ready;
    }
    /**
     * Promiseresolve
     * @readonly
     * @type {Promise<Boolean>}
     */
    get readyPromise() {
        return super.readyPromise;
    }
    /**
     * 
     * @type {Cesium.TilingScheme}
     * @readonly
     */
    get tilingScheme() {
        return super.tilingScheme;
    }
    /**
     * pick
     * @default true
     */
    get enablePickFeatures() {
        return super.enablePickFeatures;
    }
    set enablePickFeatures(val) {
        super.enablePickFeatures = val;
    }

    /**
     * {@link Cesium.ImageryProvider#ready} true
     * ready
     *
     * @param {Number} x x
     * @param {Number} y y
     * @param {Number} level 
     * @param {Number} longitude 
     * @param {Number} latitude  
     * @return {Promise.<Cesium.ImageryLayerFeatureInfo[]>|undefined} 
     */
    pickFeatures(x, y, level, longitude, latitude) {
        return super.pickFeatures(x, y, level, longitude, latitude);
    }
    /**
     * 
     * @param {Number} x x
     * @param {Number} y y
     * @param {Number} level 
     * @param {Cesium.Request} [request]
     * @returns {Promise.<HTMLImageElement|HTMLCanvasElement>|undefined} Promise Image  Canvas DOM undefined
     */
    requestImage(x, y, level, request) {
        return super.requestImage(x, y, level, request)
    }
}
 /**
 * 
 * @constant
 * @type {Object}
 */
XYZLayer.defaultFeatureInfoFormats = Object.freeze([
    Object.freeze(new Cesium.GetFeatureInfoFormat("json", "application/json")),
    Object.freeze(new Cesium.GetFeatureInfoFormat("xml", "text/xml")),
    Object.freeze(new Cesium.GetFeatureInfoFormat("text", "text/html")),
])
export default XYZLayer;