import defined from "../core/defined.js";

class WMSLayer extends Cesium.WebMapServiceImageryProvider {
    /**
     * WMS
     * TODO capability 
     * @extends Cesium.WebMapServiceImageryProvider
     * @param {*} options 
     * @param {Resource|String} options.url WMSURL. url {@link XYZLayer}
     * @param {String} options.layers 
     * @param {Object} [options.parameters=WMSLayer.DefaultParameters] GetMap URL
     * @param {Object} [options.getFeatureInfoParameters=WMSLayer.GetFeatureInfoDefaultParameters] GetFeatureInfo URL
     * @param {Boolean} [options.enablePickFeatures=true] pick {@link WMSLayer#pickFeatures}
     * @param {GetFeatureInfoFormat[]} [options.getFeatureInfoFormats=WMSLayer.DefaultGetFeatureInfoFormats] WMS GetFeatureInfo 
     * @param {Rectangle} [options.rectangle=Cesium.Rectangle.MAX_VALUE] 
     * @param {TilingScheme} [options.tilingScheme=new Cesium.GeographicTilingScheme()] 
     * @param {Number} [options.tileWidth=256] 
     * @param {Number} [options.tileHeight=256] 
     * @param {Number} [options.minimumLevel=0] 
     * @param {Number} [options.maximumLevel] 
     * @param {String} [options.crs] CRS  WMS version >= 1.3.0
     * @param {String} [options.srs] SRS  WMS version 1.1.0  1.1.1
     * @param {Credit|String} [options.credit] A credit for the data source, which is displayed on the canvas.
     * @param {String|String[]} [options.subdomains='abc'] {s}
     * @param {Clock} [options.clock]  Clock  `times` 
     * @param {TimeIntervalCollection} [options.times] TimeIntervalCollection 
     * 
     * @example 
     * var provider = new Cesium.WebMapServiceImageryProvider({
     *   url : 'https://sampleserver1.arcgisonline.com/ArcGIS/services/Specialty/ESRI_StatesCitiesRivers_USA/MapServer/WMSServer',
     *   layers : '0',
     *   proxy: new Cesium.DefaultProxy('/proxy/')
     * });
     *
     * viewer.addLayer(provider);
     */
    constructor(options = {}) {
        if (!defined(options.parameters)) {
            options.parameters = WMSLayer.DefaultParameters;
        }
        if (!defined(options.GetFeatureInfoDefaultParameters)) {
            options
        }
        super(options);
    }
    /**
     * 
     * @readonly
     * @type {Number}
     */
     get minimumLevel() {
        return super.minimumLevel;
    }
    /**
     * 
     * @type {Number}
     * @readonly
     */
    get maximumLevel() {
        return super.maximumLevel;
    }
    /**
     * 
     * @type {Event}
     * @readonly
     */
    get errorEvent() {
        return super.errorEvent;
    }
    /**
     * 
     * @type {Cesium.Rectangle}
     * @readonly
     */
     get rectangle() {
        return super.rectangle;
    }
    /**
     * 
     * @readonly
     * @type {Boolean}
     */
    get ready() {
        return super.ready;
    }
    /**
     * Promiseresolve
     * @readonly
     * @type {Promise<Boolean>}
     */
    get readyPromise() {
        return super.readyPromise;
    }
    /**
     * 
     * @type {Cesium.TilingScheme}
     * @readonly
     */
    get tilingScheme() {
        return super.tilingScheme;
    }
    /**
     * pick
     * @type {Boolean}
     * @default true
     */
    get enablePickFeatures() {
        return this._tileProvider.enablePickFeatures;
    }
    set enablePickFeatures(val) {
        this._tileProvider.enablePickFeatures = val;
    }
    /**
     * {@link Cesium.ImageryProvider#ready} true
     * ready
     *
     * @param {Number} x x
     * @param {Number} y y
     * @param {Number} level 
     * @param {Number} longitude 
     * @param {Number} latitude  
     * @return {Promise.<Cesium.ImageryLayerFeatureInfo[]>|undefined} 
     */
    pickFeatures(x, y, level, longitude, latitude) {
        return super.pickFeatures(x, y, level, longitude, latitude);
    }
    /**
     * 
     * @param {Number} x x
     * @param {Number} y y
     * @param {Number} level 
     * @param {Cesium.Request} [request]
     * @returns {Promise.<HTMLImageElement|HTMLCanvasElement>|undefined} Promise Image  Canvas DOM undefined
     */
    requestImage(x, y, level, request) {
        return super.requestImage(x, y, level, request)
    }
}

/**
 * GetMapWMS URL:
 *    service=WMS
 *    version=1.1.1
 *    request=GetMap
 *    styles=
 *    format=image/png,
 *    transparent=true
 *
 * @constant
 * @static
 * @type {Object}
 */
WMSLayer.DefaultParameters = Object.assign({
    transparent: true,
}, Cesium.WebMapServiceImageryProvider.DefaultParameters, {
    format: 'image/png'
})
/**
 * WMS GetFeatureInfoURL:
 *     service=WMS
 *     version=1.1.1
 *     request=GetFeatureInfo
 *
 * @constant
 * @static
 * @type {Object}
 */
WMSLayer.GetFeatureInfoDefaultParameters = Cesium.WebMapServiceImageryProvider.GetFeatureInfoDefaultParameters;
export default WMSLayer;