
import defaultValue from '../core/defaultValue';
import PointBaseGraphic from './PointBaseGraphic';
import glsl from '../shader/radarScan'
const {
    MaterialAppearance,
    Material
} = Cesium;
class RadarScanGraphic extends PointBaseGraphic {
    /**
     * 
     * @extends PointBaseGraphic
     * @param {CircleScanGraphic.ConstructorOptions} options 
     * @example
     * graphic = new CesiumPro.RadarScanGraphic({
     *   position: new CesiumPro.LonLat(107.766, 34.548),
     *   radius: 1000,
     *   color: '#FF0000',
     *   speed: 5000
     * });
     * graphic.addTo(viewer);
     * // or
     * viewer.graphicGraphic.add(graphic);
     * @demo {@link examples/apps/index.html#/5.3.3-radarScan|}
     */
    constructor(options) {
        super(options);
        this._speed = defaultValue(options.speed, 10);
        this._time = 0.0;
        this.createGraphic()
    }
    /**
     * 
     * @type {Number}
     * @default 10
     */
     get speed() {
        return this._speed;
    }
    set speed(val) {
        if (val === this._speed) {
            return;
        }
        if (val < 0) {
            this._speed = 0;
        }
        this.definedChanged.raise('speed', val, this._speed);
        this._speed = val;
    }
    createAppearance() {
        if (this._appearance) {
            return this._appearance;
        }
        this._appearance = new MaterialAppearance({
            material: new Material({
                translucent: true,
                fabric: {
                    uniforms: {
                        color: this._color,
                        time: 0.0,
                    },
                    source: glsl
                },
            })
        })
        return this._appearance;
    }
    update(time) {
        if (!this.material) {
            return;
        }
        if (this.speed === 0) {
            this.material.uniforms.time = 0.9999;
            return;
        }
        const delta = 500
        const t = Cesium.JulianDate.toDate(time).getTime() / delta * this.speed;
        this.material.uniforms.time = t - parseInt(t);
        
    }
}
/**
 * @typedef {Object} RadarScanGraphic.ConstructorOptions
 *
 * 
 *
 * @property {LonLat} position 
 * @property {Number} radius 
 * @property {Cesium.Color | String} [color] 
 * @property {Boolean} [clampToGround = false] 
 * @property {Number} [height = 0]  
 * @property {Number} [extrudedHeight = 0]  
 * @property {Number} [stRotation = 0] 
 * @property {Object} [property] 
 * @property {Number} [speed = 10] 
 */
export default RadarScanGraphic;