/**
 * 
 * @exports CartometryType
 * @enum {Number}
 */
const CartometryType = {
  /**
   * 
   * @type {Number}
   * @constant
   */
  SURFACE_DISTANCE: 1,
  /**
   * 
   * @type {Number}
   * @constant
   */
  SPACE_DISTANCE: 2,
  /**
   * 
   * @type {Number}
   * @constant
   */
  SPACE_AREA: 3,
  /**
   * 
   * @type {Number}
   * @constant
   */
  SURFACE_AREA: 4,
  /**
   * 
   * @type {Number}
   * @constant
   */
  HEIGHT: 5,
  /**
   * 
   * @type {Number}
   * @constant
   */
  ANGLE: 6,
};

/**
 * 
 * @param {CartometryType}
 * @returns {Number} true
 */
CartometryType.validate = function (type) {
  return type === CartometryType.SURFACE_AREA || type === CartometryType.SURFACE_DISTANCE
    || type === CartometryType.SPACE_AREA || type === CartometryType.SPACE_DISTANCE
    || type === CartometryType.HEIGHT || type === CartometryType.ANGLE;
};

/**
 * 
 * @param  {CartometryType} value 
 * @returns {String} 
 */
CartometryType.getKey = function (value) {
  let key;
  switch (value) {
    case 1:
      key = 'SURFACE_DISTANCE';
      break;
    case 2:
      key = 'SPACE_DISTANCE';
      break;
    case 3:
      key = 'SPACE_AREA';
      break;
    case 4:
      key = 'SURFACE_AREA';
      break;
    case 5:
      key = 'HEIGHT';
      break;
    case 6:
      key = 'ANGLE';
      break;
    default:
      key = undefined;
  }
  return key;
};
/**
 * key
 * @param {*} value 
 * @returns {String} 
 */
CartometryType.getValue = function (value) {
  let key;
  switch (value) {
    case 1:
      key = '';
      break;
    case 2:
      key = '';
      break;
    case 3:
      key = '';
      break;
    case 4:
      key = '';
      break;
    case 5:
      key = '';
      break;
    case 6:
      key = '';
      break;
    default:
      key = undefined;
  }
  return key;
};
export default Object.freeze(CartometryType);
