import defaultValue from "../core/defaultValue.js";

const {
    BingMapsImageryProvider,
    BingMapsStyle 
} = Cesium;
class BingLayer extends BingMapsImageryProvider {
    /**
     * 
     * @param {Object} [options] 
     * @param {String} [options.mapStyle = BingLayer.mapStyle.AERIAL] 
     * @param {String} [options.tileProtocol] (http,https
     * @param {String} [options.key] keyhttps://www.bingmapsportal.com/
     */
    constructor(options = {}) {
        options.url = 'https://dev.virtualearth.net'
        options.mapStyle = defaultValue(options.mapStyle, BingLayer.mapStyle.AERIAL);
        options.key = defaultValue(options.key, 'AqMhBWJKbBPBtoWEvtGdUii6XSkDCJ3vWFpOVWzplD-Q0J-ECUF6i8MGXpew8bkc')
        super(options);
    }
    /**
     * 
     * @readonly
     * @type {Number}
     */
    get minimumLevel() {
        return super.minimumLevel;
    }
    /**
     * 
     * @type {Number}
     * @readonly
     */
    get maximumLevel() {
        return super.maximumLevel;
    }
    /**
     * 
     * @type {Event}
     * @readonly
     */
    get errorEvent() {
        return super.errorEvent;
    }
    /**
     * 
     * @readonly
     * @type {Boolean}
     */
    get ready() {
        return super.ready;
    }
    /**
     * Promiseresolve
     * @readonly
     * @type {Promise<Boolean>}
     */
    get readyPromise() {
        return super.readyPromise;
    }
    /**
     * 
     * @type {Cesium.TilingScheme}
     * @readonly
     */
    get tilingScheme() {
        return super.tilingScheme;
    }
    /**
     * 
     * @type {Cesium.Rectangle}
     * @readonly
     */
    get rectangle() {
        return super.rectangle;
    }
    /**
     * pick
     * @type {Boolean}
     * @default true
     */
    get enablePickFeatures() {
        return this._tileProvider.enablePickFeatures;
    }
    set enablePickFeatures(val) {
        this._tileProvider.enablePickFeatures = val;
    }
    /**
     * {@link Cesium.ImageryProvider#ready} true
     * ready
     *
     * @param {Number} x x
     * @param {Number} y y
     * @param {Number} level 
     * @param {Number} longitude 
     * @param {Number} latitude  
     * @return {Promise.<Cesium.ImageryLayerFeatureInfo[]>|undefined} 
     */
    pickFeatures(x, y, level, longitude, latitude) {
        return super.pickFeatures(x, y, level, longitude, latitude);
    }
    /**
     * 
     * @param {Number} x x
     * @param {Number} y y
     * @param {Number} level 
     * @param {Cesium.Request} [request]
     * @returns {Promise.<HTMLImageElement|HTMLCanvasElement>|undefined} Promise Image  Canvas DOM undefined
     */
    requestImage(x, y, level, request) {
        return super.requestImage(x, y, level, request)
    }
    /**
     * xylevel Bing key
     * @param {Number} x 
     * @param {Number} y 
     * @param {Number} level 
     */
    static tileXYToQuadKey(x, y, level) {
        return BingMapsImageryProvider.tileXYToQuadKey(x, y, level);
    }
    /**
     *  Bing key (x, y, level) 
     * @param {String} quadkey 
     */
    static quadKeyToTileXY(quadkey) {
        return BingMapsImageryProvider.quadKeyToTileXY(quadkey)
    }
    /**
     * Bing
     * <code>
     * <ul>
     *  <li>AERIAL</li>
     *  <li>AERIAL_WITH_LABELS<li>
     *  <li>AERIAL_WITH_LABELS_ON_DEMAND</li>
     *  <li>ROAD: </i>
     *  <li>CANVAS_DARK: </i>
     *  <li>CANVAS_LIGHT: </i>
     *  <li>CANVAS_GRAY: </i>
     * </ul>
     * </code>
     * @memberof BingLayer
     * @static
     * @type {Object}
     */
    static mapStyle = BingMapsStyle;
}
export default BingLayer;