import CesiumProError from "../core/CesiumProError.js";
import defined from "../core/defined.js";
import proj from '../core/proj'
import GeoJsonDataSource from "./GeoJsonDataSource.js";
const {
    Resource
} = Cesium;
class WFSLayer {
    /**
     * WFS
     * @param {Object} options 
     * @param {String} options.url WFS
     * @param {String} options.typeName 
     * @param {WFSLayer.Style} [options.style] 
     * @example
     * const wfs = new CesiumPro.WFSLayer({
     *     url: "http://localhost:8080/geoserver/tiger/ows",
     *     typeName: "tiger:poi",
     *     style: {
     *         pointColor: Cesium.Color.GOLD
     *     }
     * })
     * viewer.addLayer(wfs)
     */
    constructor(options = {}) {
        this._url = options.url;
        this._typeName = options.typeName;
        this._style = options.style;
        if (!defined(this._url)) {
            throw new CesiumProError('parameter url must be provided.')
        }
        if (!defined(this._typeName)) {
            throw new CesiumProError('parameter typeName must be provided.')
        }
        const resource = new Resource({
            url: options.url,
            queryParameters: {
                service: 'WFS',
                version: "1.0.0",
                request: "GetFeature",
                typeName: options.typeName,
                maxFeatures: 50,
                outputFormat: 'application/json'
            }
        })
        this._dataSource = GeoJsonDataSource.load(resource, this._style);
    }
    get rectangle() {
        return this._rectangle || this.tilingScheme.rectangle;
    }
    get tilingScheme() {
        return proj.get('EPSG:4326');
    }
    get tileWidth() {
        return this._tileWidth;
    }
    get tileHeight() {
        return this._tileHeight
    }
    get ready() {
        return this._ready;
    }
    get readyPromise() {
        return this._readyPromise;
    }
}
/**
 * @typedef {Object} WFSLayer.Style
 *
 * GeoJson
 *
 * @property {Cesium.Color} [pointColor] GeoJsonDataSource.pointColor
 * @property {Number} [pointSize] GeoJsonDataSource.pointSize
 * @property {Cesium.Color} [outlineColor] GeoJsonDataSource.outlineColor
 * @property {Number} [outlineWidth] GeoJsonDataSource.outlineWidth
 * @property {Boolean} [outline] GeoJsonDataSource.outline
 * @property {Cesium.Color} [fill] GeoJsonDataSource.fill
 * @property {Cesium.Color} [lineColor] GeoJsonDataSource.lineColor
 * @property {Cesium.Color} [lineWidth] GeoJsonDataSource.lineWidth
 * @property {Boolean} [clampToGround] GeoJsonDataSource.clampToGround
 * @property {String|Cesium.Credit} [credit] 
 */
export default WFSLayer;