import CesiumProError from "../core/CesiumProError";
import checkViewer from "../core/checkViewer";
import defaultValue from "../core/defaultValue";
import Event from '../core/Event'
import guid from "../core/guid";
class BaseAnalysis {
    /**
     * 
     * @param {Cesium.Viewer} viewer viewer
     * @param {object} options 
     */
    constructor(viewer, options = {}) {
        checkViewer(viewer);
        this._id = defaultValue(options.id, guid());
        this.options = options;
        this._viewer = viewer;
        this._doing = false;
        /**
         * 
         * @type {Event}
         * @readonly
         */
        this.preDo = new Event();
        /**
         * 
         * @type {Event}
         * @readonly
         */
        this.postDo = new Event();
    }
    /**
     * @readonly
     * @type {string}
     * 
     */
    get type() {
        return this._type;
    }
    /**
     * 
     * @param {*} options 
     */
    do(options) {
       
    }
    isDestroyed() {
        return false;
    }
    _check() {
        if (this._doing) {
            throw new CesiumProError('...');
        }
        this._doing = true;
    }
}
export default BaseAnalysis;