const {WebMapTileServiceImageryProvider} = Cesium;
class WMTSLayer extends WebMapTileServiceImageryProvider {
    /**
     * WMTS
     * TODO capability 
     * 
     * @param {Object} options 
     * @param {Resource|String} options.url WMTSGetTileURLtile-URL. tile-URLmURL
     * <ul>
     *  <li><code>{style}</code>: </li>
     *  <li><code>{TileMatrixSet}</code>: tile</li>
     *  <li><code>{TileMatrix}</code>:</li>
     *  <li><code>{TileRow}</code>: </li>
     *  <li><code>{TileCol}</code>: </li>
     *  <li><code>{s}</code>: </li>
     * </ul>
     * @extends Cesium.WebMapTileServiceImageryProvider
     * @param {String} [options.format='image/jpeg'] (MIME type)
     * @param {String} options.layer .
     * @param {String} options.style .
     * @param {String} options.tileMatrixSetID  WMTS  TileMatrixSet 
     * @param {Array} [options.tileMatrixLabels] TileMatrix  WMTS 
     * @param {Clock} [options.clock]  Clock 
     * @param {TimeIntervalCollection} [options.times] TimeIntervalCollection data
     * @param {Object} [options.dimensions] 
     * @param {Number} [options.tileWidth=256] 
     * @param {Number} [options.tileHeight=256] 
     * @param {TilingScheme} [options.tilingScheme] 
     * @param {Rectangle} [options.rectangle=Cesium.Rectangle.MAX_VALUE] 
     * @param {Number} [options.minimumLevel=0] 
     * @param {Number} [options.maximumLevel] 
     * @param {Credit|String} [options.credit] 
     * @param {String|String[]} [options.subdomains='abc'] {s}
     *
     
     *
     * Provides tiled imagery served by {@link http://www.opengeospatial.org/standards/wmts|WMTS 1.0.0} compliant servers.
     * This provider supports HTTP KVP-encoded and RESTful GetTile requests, but does not yet support the SOAP encoding.
     *
     * @example
     * // Example 1. USGS shaded relief tiles (KVP)
     * var shadedRelief1 = new CesiumPro.WMTSLayer({
     *     url : 'http://basemap.nationalmap.gov/arcgis/rest/services/USGSShadedReliefOnly/MapServer/WMTS',
     *     layer : 'USGSShadedReliefOnly',
     *     style : 'default',
     *     format : 'image/jpeg',
     *     tileMatrixSetID : 'default028mm',
     *     // tileMatrixLabels : ['default028mm:0', 'default028mm:1', 'default028mm:2' ...],
     *     maximumLevel: 19,
     *     credit : new Cesium.Credit('U. S. Geological Survey')
     * });
     * viewer.imageryLayers.addImageryProvider(shadedRelief1);
     *
     * @example
     * // Example 2. USGS shaded relief tiles (RESTful)
     * var shadedRelief2 = new CesiumPro.WMTSLayer({
     *     url : 'http://basemap.nationalmap.gov/arcgis/rest/services/USGSShadedReliefOnly/MapServer/WMTS/tile/1.0.0/USGSShadedReliefOnly/{Style}/{TileMatrixSet}/{TileMatrix}/{TileRow}/{TileCol}.jpg',
     *     layer : 'USGSShadedReliefOnly',
     *     style : 'default',
     *     format : 'image/jpeg',
     *     tileMatrixSetID : 'default028mm',
     *     maximumLevel: 19,
     *     credit : new Cesium.Credit('U. S. Geological Survey')
     * });
     * viewer.addLayer(shadedRelief2);
     *
     * @example
     * // Example 3. NASA time dynamic weather data (RESTful)
     * var times = Cesium.TimeIntervalCollection.fromIso8601({
     *     iso8601: '2015-07-30/2017-06-16/P1D',
     *     dataCallback: function dataCallback(interval, index) {
     *         return {
     *             Time: Cesium.JulianDate.toIso8601(interval.start)
     *         };
     *     }
     * });
     * var weather = new CesiumPro.WMTSLayer({
     *     url : 'https://gibs.earthdata.nasa.gov/wmts/epsg4326/best/AMSR2_Snow_Water_Equivalent/default/{Time}/{TileMatrixSet}/{TileMatrix}/{TileRow}/{TileCol}.png',
     *     layer : 'AMSR2_Snow_Water_Equivalent',
     *     style : 'default',
     *     tileMatrixSetID : '2km',
     *     maximumLevel : 5,
     *     format : 'image/png',
     *     clock: clock,
     *     times: times,
     *     credit : new Cesium.Credit('NASA Global Imagery Browse Services for EOSDIS')
     * });
     * viewer.addLayer(weather);
     * const geoserverWMTS = new WMTSLayer({
     *       url: 'http://localhost:8080/geoserver/gwc/service/wmts',
     *       layer: 'topp:states',
     *       format: 'image/png',
     *       tileMatrixSetID: 'EPSG:4326',
     *       tileMatrixLabels: ['EPSG:4326:0','EPSG:4326:1','EPSG:4326:2', 'EPSG:4326:3', 'EPSG:4326:4'],
     *       style: '',
     *       tileWidth:700,
     *       tileHeight:300,
     *       tilingScheme:proj.get('EPSG:4326')
     * })
     * viewer.addLayer(geoserverWMTS)
     */
    constructor(options = {}) {
        super(options)
    }
    /**
     * 
     * @type {Object}
     */
    get dimensions() {
        return super.dimensions;
    }
    set dimensions(val) {
        super.dimensions = val;
    }
    /**
     * 
     * @readonly
     * @type {Number}
     */
    get minimumLevel() {
        return super.minimumLevel;
    }
    /**
     * 
     * @type {Number}
     * @readonly
     */
    get maximumLevel() {
        return super.maximumLevel;
    }
    /**
     * 
     * @type {Event}
     * @readonly
     */
    get errorEvent() {
        return super.errorEvent;
    }
    /**
     * 
     * @readonly
     * @type {Boolean}
     */
     get ready() {
        return super.ready;
    }
    /**
     * Promiseresolve
     * @readonly
     * @type {Promise<Boolean>}
     */
    get readyPromise() {
        return super.readyPromise;
    }
    /**
     * 
     * @type {Cesium.TilingScheme}
     * @readonly
     */
    get tilingScheme() {
        return super.tilingScheme;
    }
    /**
     * 
     * @type {Cesium.Rectangle}
     * @readonly
     */
     get rectangle() {
        return super.rectangle;
    }
    /**
     * pick
     * @type {Boolean}
     * @default true
     */
     get enablePickFeatures() {
        return this._tileProvider.enablePickFeatures;
    }
    set enablePickFeatures(val) {
        this._tileProvider.enablePickFeatures = val;
    }
    /**
     * {@link Cesium.ImageryProvider#ready} true
     * ready
     *
     * @param {Number} x x
     * @param {Number} y y
     * @param {Number} level 
     * @param {Number} longitude 
     * @param {Number} latitude  
     * @return {Promise.<Cesium.ImageryLayerFeatureInfo[]>|undefined} 
     */
    pickFeatures(x, y, level, longitude, latitude) {
        return super.pickFeatures(x, y, level, longitude, latitude);
    }
    /**
     * 
     * @param {Number} x x
     * @param {Number} y y
     * @param {Number} level 
     * @param {Cesium.Request} [request]
     * @returns {Promise.<HTMLImageElement|HTMLCanvasElement>|undefined} Promise Image  Canvas DOM undefined
     */
    requestImage(x, y, level, request) {
        return super.requestImage(x, y, level, request)
    }
}
export default WMTSLayer;